* {
	user-select: none;
	outline: none;
	-webkit-tap-highlight-color: transparent;
	text-decoration: none;
	touch-action: none;
}

body {
	background-color: #d1d1d1;
	margin: 0px;
	overflow: hidden;
	background-size: 40px 40px;
	background-image:
	  linear-gradient(to right, rgba(0,0,0,0.06) 2px, transparent 2px),
	  linear-gradient(to bottom, rgba(0,0,0,0.06) 2px, transparent 2px);
}

model-viewer {
	width: 2px !important;
	height: 2px !important;
	position: fixed !important;
	opacity: 0;
	top: calc(10vh - 2px);
}

canvas {
	position: fixed;
	top: 80px;
	left: 0px;
}

.spin {
	width: 56px;
	height: 56px;
	position: fixed;
	top: calc(50vh - 120px);
	left: calc(50vw - 27px);
	opacity: 0.7;
	display: none;
}

svg {
	width: 100%;
	height: 100%;
}

.arb {
	width: 80px;
	height: 80px;
	background-color: red;
}