.ItemScrollerHold {
	position: fixed;
	width: 84px;
	height: 84px;
    border: 3px solid #111;
	top: calc(100vh - 225px);
    left: calc(50vw - 45.5px);
	border-radius: 60px;
	background-color: rgba(0,0,0,0.08);
	z-index: 18;
}

.ItemScrollerText {
	position: fixed;
	width: 200px;
	height: 30px;
    line-height: 3px;
	top: calc(100vh - 110px);
    left: calc(50vw - 100px);
    text-align: center;
    font-weight: bold;
	z-index: 18;
}

.ItemScrollerFace {
	width: 100vw;
	height: 250px;
	position: fixed;
	top: calc(100vh - 250px);
	left: 0px;
	white-space: nowrap;
	overflow: hidden;
	z-index: 15;
	touch-action: auto;
	background-color: #EEE;
	box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
}

.ItemScrollerTray {
	width: 100vw;
	height: 250px;
	position: fixed;
	top: calc(100vh - 220px);
	left: 0px;
	white-space: nowrap;
	overflow: hidden;
	z-index: 20;
	touch-action: auto;
}

.ScrollerItem {
	width: 75px;
	height: 75px;
	margin-right: 15px;
    margin-top: 2.5px;
	display: inline-block;
	overflow: hidden;
	touch-action: auto;
	background-color: rgba(0,0,0,0.08);
	border-radius: 60px;
	transition: all 0.2s;
    transform: scale(0.75);
	cursor: pointer;
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
}

.ScrollerItem:first-child {
	margin-left: calc(50vw - 40px);
}

.ScrollerItem:last-child {
	margin-right: calc(50vw - 40px);
}

.ScrollerItemOn {
    transform: scale(1);
	opacity: 0.8;
}

.ItemScrollerHead {
	position: fixed;
	height: 80px;
	width: 100vw;
	background-color: #EEE;
	top: 0px;
	left: 0px;
	box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
	background-image: url(./mate_logo.png);
	background-repeat: no-repeat;
	background-position: center;
	background-size: auto 80%;
}

@media (orientation: landscape) {
	.ItemScrollerHold {
		top: calc(100vh - 145px);
	}
	
	.ItemScrollerText {
		top: calc(100vh - 30px);
	}
	
	.ItemScrollerFace {
		top: calc(100vh - 180px);
	}
	
	.ItemScrollerTray {
		top: calc(100vh - 140px);
	}
}